import axios from 'axios';

const defaultClient = axios.create();

export { defaultClient };

const { CancelToken } = axios;
export { CancelToken };

export function login({ baseURL, token, username, password, bearerToken }) {
  if (baseURL) {
    defaultClient.defaults.baseURL = baseURL;
  }
  if (token) {
    defaultClient.defaults.headers.Authorization = `token ${token}`;
  }
  if (bearerToken) {
    defaultClient.defaults.headers.Authorization = `Bearer ${bearerToken}`;
  }
  if (username || password) defaultClient.defaults.auth = { username, password };
}

export function logout() {
  delete defaultClient.defaults.headers.Authorization;
  delete defaultClient.defaults.auth;
}

function btoa(str) {
  const buffer = str instanceof Buffer ? str : Buffer.from(str.toString(), 'binary');
  return buffer.toString('base64');
}

export function debounce(func, wait, immediate) {
  let timeout;
  return (args) => {
    const context = this;
    const later = () => {
      timeout = null;
      if (!immediate) func.apply(context, args);
    };
    const callNow = immediate && !timeout;
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);
    if (callNow) func.apply(context, args);
  };
}

export function uuidv4() {
  return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, (c) =>
    // eslint-disable-next-line
    (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16));
}

function setDefaultHeaders({
  username = false,
  password = false,
  token,
  contentType = 'application/json',
  accept = 'application/json',
  ...props
} = {}) {
  const headers = {
    'content-type': contentType,
    Accept: accept,
  };
  if (username) {
    const encodedAuth = btoa(`${username}:${password}`);
    headers.Authorization = `Basic ${encodedAuth}`;
  } else if (token) {
    headers.Authorization = `token ${token}`;
  }
  return { ...props, ...headers };
}

const KV_PARAMS = [
  'jobmetadata',
  'jobMetadata',
  'notificationdata',
  'notificationData',
  'methodMetadata',
];

function setQueryParams(queryParams) {
  const paramKeys = Object.keys(queryParams);
  if (paramKeys.length === 0) return '';

  const reducer = (accumulator, currentKey, currentIndex) => {
    if (typeof queryParams[currentKey] === 'undefined') {
      return accumulator;
    }
    const currentValue = queryParams[currentKey];
    const separator = currentIndex > 0 ? '&' : '';
    if (KV_PARAMS.includes(currentKey)) {
      const kvReducer = (a, c, i) => `${a}${i > 0 ? '&' : ''}${currentKey}=${c.key}%3D${c.value}`;
      const kvString = currentValue.reduce(kvReducer, '');
      return `${accumulator}${separator}${kvString}`;
    }
    if (Array.isArray(currentValue)) {
      return `${accumulator}${separator}${currentKey}=${currentValue.join(',')}`;
    }
    return `${accumulator}${separator}${currentKey}=${currentValue}`;
  };
  const paramStr = paramKeys.reduce(reducer, '?');
  return paramStr;
}

function setMatrix(matrix) {
  let matrixList = matrix;
  if (!Array.isArray(matrix)) {
    matrixList = Object.entries(matrix);
  }
  if (matrixList.length === 0) return '';
  const reducer = (a, c) => {
    let matrixKey;
    let matrixValue;
    if (Array.isArray(c)) {
      [matrixKey, matrixValue] = c;
    } else {
      [matrixKey] = Object.keys(c);
      [matrixValue] = Object.values(c);
    }
    if (matrixValue === '' || matrixValue === undefined) {
      return a;
    }
    return `${a};${matrixKey}=${encodeURIComponent(matrixValue)}`;
  };
  const matrixStr = matrixList.reduce(reducer, '');
  return matrixStr;
}

export function isRequired() {
  throw new Error('Missing property');
}

export function vFetch({
  path,
  method = 'GET',
  body: data,
  headers: extraHeaders = {},
  queryParams = {},
  matrixParams = [],
  costEstimate = false,
  ...props
} = {}) {
  const params = setQueryParams(queryParams);
  const matrix = setMatrix(matrixParams);
  const headers = setDefaultHeaders(extraHeaders);
  let url = `${path}${matrix}${params}`;
  if (costEstimate) {
    url = url.replace('/API/', '/API/cost/');
  }
  const request = {
    method,
    url,
    headers,
    data,
    ...props,
  };
  return new Promise((resolve, reject) => {
    defaultClient(request)
      .then((response) => {
        response.ok = response.status >= 100 && response.status <= 399;
        response.json = () => response.data;
        response.text = () => response.data;
        resolve(response);
      })
      .catch((error) => reject(error));
  });
}

export default vFetch;
